export const createMealPlanData = (mealPlan) => {
  return (dispatch, getState, { getFirebase }) => {
    //make async call to database
    const profile = getState().firebase.profile;
    const authUID = getState().firebase.auth.uid;

    var uid;
    switch (profile.type) {
      case "business_admin":
        uid = authUID;
        break;
      case "business_sub":
        uid = profile.admin;
        break;
      case "academic_admin":
        uid = authUID;
        break;
      case "academic_sub":
        uid = profile.admin;
        break;
      case "household_admin":
        uid = authUID;
        break;
      case "household_sub":
        uid = profile.admin;
        break;
      default:
        uid = authUID;
        break;
    }

    getFirebase()
      .firestore()
      .collection("marketplace")
      .doc(uid)
      .collection("mealPlanData")
      .doc(mealPlan.month)
      .collection(mealPlan.day)
      .add(mealPlan.upload)
      .then((docRef) => {
        // make the docId easily accessible so that we can delete it later if we want.
        getFirebase()
          .firestore()
          .collection("marketplace")
          .doc(uid)
          .collection("mealPlanData")
          .doc(mealPlan.month)
          .collection(mealPlan.day)
          .doc(docRef.id)
          .set({ id: docRef.id }, { merge: true });
        dispatch({ type: "CREATE_MEAL", mealPlan });
      })
      .catch((err) => {
        dispatch({ type: "CREATE_MEAL_ERROR", err });
      });
  };
};

export const getMealData = (meals) => {
  return (dispatch, getState, { getFirebase }) => {
    //make async call to database
    const profile = getState().firebase.profile;
    const authUID = getState().firebase.auth.uid;

    var uid;
    switch (profile.type) {
      case "business_admin":
        uid = authUID;
        break;
      case "business_sub":
        uid = profile.admin;
        break;
      case "academic_admin":
        uid = authUID;
        break;
      case "academic_sub":
        uid = profile.admin;
        break;
      case "household_admin":
        uid = authUID;
        break;
      case "household_sub":
        uid = profile.admin;
        break;
      default:
        uid = authUID;
        break;
    }

    getFirebase()
      .firestore()
      .collection("marketplace")
      .doc(uid)
      .collection("mealPlanData")
      .doc(meals.month)
      .collection(meals.day)
      .get()
      .then((snapshot) => {
        const mealPlan = [];
        snapshot.forEach((doc) => {
          mealPlan.push(doc.data());
        });
        dispatch({ type: "GET_MEALS", payload: mealPlan });
      })
      .catch((err) => {
        dispatch({ type: "GET_MEALS_ERROR", err });
      });
  };
};

